<script>
import store from '@/store'

import BookItem from '@/components/BookItem.vue';
// import BookAdaptationItem from '@/components/bookpagecomponents/BookAdaptationItem.vue';
// import BookQuoteItem from '@/components/bookpagecomponents/BookQuoteItem.vue';

import { TimelineLite, Sine, gsap, CSSPlugin } from 'gsap'

gsap.registerPlugin(CSSPlugin);




export default {
    name: "BookPage",
    components: {
        BookItem,
        // BookSummaryItem,
        // BookAdaptationItem
    },
    data(){
        return{
            expandedSummary: true,
            expandedDownloads: false,
            expandedAdaptations: true,
            books: store.books,
            selectedMovie: false,
            adaptations: store.adaptations,
            selectedMovieImage: "help",
        }
    },
    props:{
        slug: {
            type: String,
            required: true
        },
    },
    methods: {
         select(){
            this.selectedMovieImage = this.image;
        }
        },
    computed: {
        book(){
            return store.books.find(
                book => book.slug === this.slug
            )
        },
       
    },
    mounted (){
      const { bookAnimation } = this.$refs;
      const timelineBook = new TimelineLite();

      timelineBook.to(bookAnimation, 1, {
           y: -500,
           x: 200,
           ease: Sine.easeIn,
        }) 

    } 
}
</script>


